


















































































.surveillance{
  height: 100vh;
  background: url(../assets/imgs/surveillance/bg.png) no-repeat;
  background-size: 100% 100%;
  box-sizing: border-box;
  padding-top: 150px;
  /* 标题开始 */
      .title{
        position: absolute;
        top: 0;
        width: 100%;
        height: 80px;
        text-align: center;
        color: #f6f6f8;
        background: url(../assets/imgs/index/head.png) no-repeat;
        background-size: 100%;
        background-position: bottom center;
        z-index: 10;
        .txt-center{
              h1{
                font-size: 35px;
                font-weight: 400;
              }
              p{
                font-size: 14px;
                font-weight: 400;
              }
            }
      }
      /* 标题结束 */
  .num{
    width: 680px;
    margin: 0 auto;
    color: #fff;
    li{
      display: inline-block;
      margin: 0 50px;
      p{
        text-align: center;
        font-size: 25px;
      }
      p:nth-child(2){
        font-weight: bold;
      }
      p:nth-child(1) span{
        display: inline-block;
        width: 10px;
        height: 10px;
        border-radius: 50px;
        background: #76FBFD;
      }
    }
  }


  .video{
    text-align: center;
  }


  .itemlist{
    display: inline-block;
    margin: 40px 0;
    transform-style: preserve-3d;
    perspective: 800px;
    .item{
      display: inline-block;
      margin: 0 20px;
      width: 300px;
      height: 200px;
      background: url(../assets/imgs/machine/1.png);
      background-size: 100% 100%;
    }
    .item:first-child{
      transform: rotateY(30deg);
      transform-origin: right;
    }
    .item:last-child{
      transform: rotateY(-30deg);
      transform-origin: left;
    }
  }

  .backWrap{
    .back{
      width: 160px;
      height: 15px;
      font-size: 13px;
      margin: 50px auto;
      color: #4FDDF8;
      line-height: 15px;
      span{
        vertical-align: top;
        margin: 0 10px;
      }
      img:nth-child(1){
        transform: rotate(-180deg);
      }
    }
    .back:hover{
      cursor: pointer;
    }
  }

}
