.surveillance {
  height: 100vh;
  background: url(../assets/imgs/surveillance/bg.png) no-repeat;
  background-size: 100% 100%;
  box-sizing: border-box;
  padding-top: 150px;
  /* 标题开始 */
  /* 标题结束 */
}
.surveillance .title {
  position: absolute;
  top: 0;
  width: 100%;
  height: 80px;
  text-align: center;
  color: #f6f6f8;
  background: url(../assets/imgs/index/head.png) no-repeat;
  background-size: 100%;
  background-position: bottom center;
  z-index: 10;
}
.surveillance .title .txt-center h1 {
  font-size: 35px;
  font-weight: 400;
}
.surveillance .title .txt-center p {
  font-size: 14px;
  font-weight: 400;
}
.surveillance .num {
  width: 680px;
  margin: 0 auto;
  color: #fff;
}
.surveillance .num li {
  display: inline-block;
  margin: 0 50px;
}
.surveillance .num li p {
  text-align: center;
  font-size: 25px;
}
.surveillance .num li p:nth-child(2) {
  font-weight: bold;
}
.surveillance .num li p:nth-child(1) span {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 50px;
  background: #76FBFD;
}
.surveillance .video {
  text-align: center;
}
.surveillance .itemlist {
  display: inline-block;
  margin: 40px 0;
  transform-style: preserve-3d;
  perspective: 800px;
}
.surveillance .itemlist .item {
  display: inline-block;
  margin: 0 20px;
  width: 300px;
  height: 200px;
  background: url(../assets/imgs/machine/1.png);
  background-size: 100% 100%;
}
.surveillance .itemlist .item:first-child {
  transform: rotateY(30deg);
  transform-origin: right;
}
.surveillance .itemlist .item:last-child {
  transform: rotateY(-30deg);
  transform-origin: left;
}
.surveillance .backWrap .back {
  width: 160px;
  height: 15px;
  font-size: 13px;
  margin: 50px auto;
  color: #4FDDF8;
  line-height: 15px;
}
.surveillance .backWrap .back span {
  vertical-align: top;
  margin: 0 10px;
}
.surveillance .backWrap .back img:nth-child(1) {
  transform: rotate(-180deg);
}
.surveillance .backWrap .back:hover {
  cursor: pointer;
}
